
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { saveAssessmentResponse, updateAssessment } from '../../lib/database';

interface Question {
  id: number;
  text: string;
  options?: string[]; // present for multiple-choice
  type?: 'choice' | 'text'; // text for open-ended (Q17)
}

const questions: Question[] = [
  {
    id: 1,
    text: 'How often are access permissions updated when residents move out or staff leave?',
    options: [
      'Rarely or never',
      'Every few months',
      'Monthly',
      'Weekly / automated with tenancy data',
    ],
  },
  {
    id: 2,
    text: 'What type of access system do you currently use?',
    options: ['Manual keys', 'Key cards/fobs', 'PIN codes / shared codes', 'Mobile app / integrated smart access'],
  },
  {
    id: 3,
    text: 'What level of security staffing do you have?',
    options: ['None', 'Ad hoc / part-time contractors', 'Daytime only', '24/7 coverage with trained staff'],
  },
  {
    id: 4,
    text: 'Do you have dedicated security or concierge staff on-site?',
    options: ['None', 'Part-time', 'Daytime only', '24/7 trained staff'],
  },
  {
    id: 5,
    text: 'How are visitor and contractor accesses managed?',
    options: ['Manual sign-ins / paper log', 'Reception staff only', 'Digital booking with ID-check', 'Integrated visitor management system'],
  },
  {
    id: 6,
    text: 'Do you have a clear incident reporting & escalation procedure?',
    options: ['None in place', 'Informal process', 'Written policy', 'Formalised, staff-trained, and audited'],
  },
  {
    id: 7,
    text: 'How visible and approachable are your staff to residents?',
    options: ['Rarely seen', 'Occasionally', 'Regularly visible', 'Highly visible'],
  },
  {
    id: 8,
    text: 'How are incidents detected?',
    options: ['Not monitored', 'Reviewed after incidents', 'Manually monitored', 'Automated alerts with escalation'],
  },
  {
    id: 9,
    text: 'How often do residents receive safety communications?',
    options: ['Never', 'Only at move-in', 'Occasionally', 'Regular, structured updates'],
  },
  {
    id: 10,
    text: 'How confident do you feel residents are in the property’s security?',
    options: ['Very low confidence', 'Somewhat concerned', 'Generally confident', 'Very confident & reassured'],
  },
  {
    id: 11,
    text: 'How do residents report safety issues?',
    options: ['Informally / word of mouth', 'Basic email / phone', 'Through management', 'Central platform / app with tracking'],
  },
  {
    id: 12,
    text: 'How quickly are incidents typically responded to?',
    options: ['Over 30 minutes', '15–30 minutes', '10–15 minutes', 'Under 10 minutes'],
  },
  {
    id: 13,
    text: 'What best describes your CCTV coverage?',
    options: ['None', 'Entrances only', 'Entrances + communal spaces', 'Full coverage with live monitoring'],
  },
  {
    id: 14,
    text: 'How do you manage blind spots around the property (car parks, stairwells, laundry areas)?',
    options: ['No monitoring in these areas', 'Occasional checks by staff', 'Partial coverage with cameras', 'Full coverage + monitoring of all high-risk areas'],
  },
  {
    id: 15,
    text: 'Do you conduct regular security audits or drills at your property?',
    options: ['Yes', 'No'],
  },
  {
    id: 16,
    text: 'Do you currently provide residents with a 24/7 emergency contact for security issues?',
    options: ['Yes', 'No'],
  },
  {
    id: 17,
    text: 'What’s the biggest challenge you currently face with your security or concierge setup?',
    type: 'text',
  },
];

export default function Assessment() {
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answers, setAnswers] = useState<any[]>([]);
  const [selectedOption, setSelectedOption] = useState<number | null>(null);
  const [textAnswer, setTextAnswer] = useState<string>('');
  const [currentUser, setCurrentUser] = useState<any>(null);
  const [currentAssessment, setCurrentAssessment] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [skipped, setSkipped] = useState<boolean>(false);
  const navigate = useNavigate();

  useEffect(() => {
    // Get user and assessment data from session storage
    const userStr = sessionStorage.getItem('currentUser');
    const assessmentStr = sessionStorage.getItem('currentAssessment');
    
    if (userStr && assessmentStr) {
      setCurrentUser(JSON.parse(userStr));
      setCurrentAssessment(JSON.parse(assessmentStr));
    } else {
      // Redirect to home if no user/assessment data
      navigate('/');
    }
  }, [navigate]);

  const handleOptionSelect = (optionIndex: number) => {
    setSelectedOption(optionIndex);
    // Selecting an option means we're not in a skipped state
    if ((questions[currentQuestion].type ?? 'choice') === 'text') {
      setSkipped(false);
    }
  };

  const saveCurrentAnswer = () => {
    const q = questions[currentQuestion];
    const newAnswers = [...answers];
    if ((q.type ?? 'choice') === 'text') {
      // If user provided text, save it; otherwise mark as skipped
      newAnswers[currentQuestion] = textAnswer.trim() !== '' ? textAnswer : '__SKIPPED__';
    } else if (selectedOption !== null) {
      newAnswers[currentQuestion] = selectedOption;
    }
    setAnswers(newAnswers);
    return newAnswers;
  };

  const loadAnswerFor = (index: number) => {
    const q = questions[index];
    const existing = answers[index];
    if ((q.type ?? 'choice') === 'text') {
      if (typeof existing === 'string') {
        if (existing === '__SKIPPED__') {
          setTextAnswer('');
          setSkipped(true);
        } else {
          setTextAnswer(existing);
          setSkipped(false);
        }
      } else {
        setTextAnswer('');
        setSkipped(false);
      }
      setSelectedOption(null);
    } else {
      setSelectedOption(typeof existing === 'number' ? existing : null);
      setTextAnswer('');
      setSkipped(false);
    }
  };

  const handleNext = async () => {
    if (!currentAssessment) return;
    
    const q = questions[currentQuestion];
    const isText = (q.type ?? 'choice') === 'text';
    const hasValidAnswer = (isText && (textAnswer.trim() !== '' || skipped)) || (!isText && selectedOption !== null);
    
    if (hasValidAnswer) {
      setLoading(true);
      
      try {
        let answerText: string;
        let score: number;
        
        if (isText) {
          answerText = skipped ? 'Skipped' : textAnswer.trim();
          score = skipped ? 0 : 1; // Basic scoring for text questions
        } else {
          answerText = q.options![selectedOption!];
          score = selectedOption!; // Use option index as score
        }
        
        // Save the current response to database
        await saveAssessmentResponse({
          assessmentId: currentAssessment.id,
          questionId: currentQuestion + 1,
          questionText: q.text,
          answerText,
          score
        });
        
        // Update local answers array
        const newAnswers = [...answers];
        newAnswers[currentQuestion] = { answerText, score };
        setAnswers(newAnswers);
        
        if (currentQuestion < questions.length - 1) {
          setCurrentQuestion(currentQuestion + 1);
          setSelectedOption(null);
          setTextAnswer('');
          setSkipped(false);
        } else {
          // Assessment complete - calculate total score and update assessment
          const totalScore = Math.round((newAnswers.reduce((sum, answer) => sum + (answer?.score || 0), 0) / (questions.length - 1)) * 100 / 3); // Adjust for scoring
          const riskLevel = totalScore >= 70 ? 'low' : totalScore >= 40 ? 'medium' : 'high';
          
          await updateAssessment(currentAssessment.id, {
            status: 'completed',
            total_score: totalScore,
            risk_level: riskLevel,
            completed_at: new Date().toISOString()
          });

          // Store final results for results page
          sessionStorage.setItem('assessmentResults', JSON.stringify({
            totalScore,
            riskLevel,
            responses: newAnswers
          }));

          navigate('/results');
        }
      } catch (error) {
        console.error('Error saving response:', error);
        alert('There was an error saving your response. Please try again.');
      } finally {
        setLoading(false);
      }
    }
  };

  const handleBack = () => {
    if (currentQuestion > 0) {
      // Optionally save current answer before going back
      saveCurrentAnswer();
      const prevIndex = currentQuestion - 1;
      setCurrentQuestion(prevIndex);
      // Load any existing answer for previous question
      setTimeout(() => loadAnswerFor(prevIndex), 0);
    }
  };

  const handleSkip = () => {
    // For the last question (text), enable completion without text
    if ((questions[currentQuestion].type ?? 'choice') === 'text') {
      setSkipped(true);
    }
  };

  const progress = ((currentQuestion + 1) / questions.length) * 100;

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg shadow-lg max-w-2xl w-full p-8">
        {/* Header */}
        <div className="text-center mb-8">
          <div className="flex items-center justify-center mb-4">
            <div className="text-2xl font-bold" style={{ color: 'hsla(187.82608696,42.99065421%,41.96078431%,1)' }}>
              NGS
            </div>
          </div>
          <h1 className="text-2xl font-semibold text-gray-800 mb-2">
            Building Safety Assessment
          </h1>
        </div>

        {/* Progress Bar */}
        <div className="mb-8">
          <div className="flex justify-between text-sm text-gray-600 mb-2">
            <span>Question {currentQuestion + 1} of {questions.length}</span>
            <span>{Math.round(progress)}% Complete</span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-2">
            <div 
              className="h-2 rounded-full transition-all duration-300"
              style={{ 
                backgroundColor: 'hsla(187.82608696,42.99065421%,41.96078431%,1)',
                width: `${progress}%`
              }}
            ></div>
          </div>
        </div>

        {/* Question */}
        <div className="mb-8">
          <h2 className="text-xl font-medium text-gray-800 mb-6 leading-relaxed">
            {questions[currentQuestion].text}
          </h2>
          {/* Options or Text Input */}
          { (questions[currentQuestion].type ?? 'choice') === 'text' ? (
            <div>
              <textarea
                value={textAnswer}
                onChange={(e) => {
                  setTextAnswer(e.target.value);
                  if (e.target.value.trim() !== '') {
                    setSkipped(false);
                  }
                }}
                placeholder="Type your response here..."
                rows={4}
                className="w-full p-4 rounded-lg border-2 border-gray-200 text-gray-700 focus:outline-none focus:ring-2"
                style={{
                  // match brand color on focus ring using inline style fallbacks
                  boxShadow: 'none',
                }}
              />
              <p className="mt-2 text-sm text-gray-500">Please provide a brief description.</p>
            </div>
          ) : (
            <div className="space-y-3">
              {questions[currentQuestion].options!.map((option, index) => (
                <button
                  key={index}
                  onClick={() => handleOptionSelect(index)}
                  className={`w-full p-4 text-left border-2 transition-all duration-200 cursor-pointer btn-angled ${
                    selectedOption === index
                      ? 'border-opacity-100 text-white'
                      : 'border-gray-200 text-gray-700 hover:border-gray-300'
                  }`}
                  style={{
                    backgroundColor: selectedOption === index
                      ? 'rgb(61, 141, 153)'
                      : 'transparent',
                    borderColor: selectedOption === index
                      ? 'rgb(61, 141, 153)'
                      : undefined,
                  }}
                >
                  <div className="flex items-center">
                    <div className={`w-4 h-4 rounded-full border-2 mr-3 flex items-center justify-center ${
                      selectedOption === index ? 'border-white' : 'border-gray-400'
                    }`}>
                      {selectedOption === index && (
                        <div className="w-2 h-2 rounded-full bg-white"></div>
                      )}
                    </div>
                    <span className="font-medium">{option}</span>
                  </div>
                </button>
              ))}
            </div>
          )}
        </div>

        {/* Navigation Buttons */}
        <div className="flex justify-between items-center">
          <button
            onClick={handleBack}
            disabled={currentQuestion === 0}
            className={`px-6 py-3 font-medium transition-colors cursor-pointer whitespace-nowrap btn-angled ${
              currentQuestion === 0
                ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
            }`}
          >
            Back
          </button>

          <div className="flex items-center gap-3">
            {currentQuestion === questions.length - 1 && (
              <button
                onClick={handleSkip}
                className="px-6 py-3 font-medium transition-colors cursor-pointer whitespace-nowrap bg-gray-100 text-gray-700 hover:bg-gray-200 btn-angled"
                title="Skip this question and finish"
              >
                Skip
              </button>
            )}

            <button
              onClick={handleNext}
              disabled={
                (questions[currentQuestion].type ?? 'choice') === 'text'
                  ? textAnswer.trim() === '' && !skipped
                  : selectedOption === null
              }
              className={`px-8 py-3 font-medium transition-colors cursor-pointer whitespace-nowrap btn-angled ${
                ((questions[currentQuestion].type ?? 'choice') === 'text'
                  ? textAnswer.trim() === '' && !skipped
                  : selectedOption === null)
                  ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                  : 'text-white'
              }`}
              style={{
                backgroundColor:
                  ((questions[currentQuestion].type ?? 'choice') === 'text'
                    ? (textAnswer.trim() !== '' || skipped)
                    : selectedOption !== null)
                    ? 'rgb(61, 141, 153)'
                    : undefined,
              }}
              onMouseEnter={(e) => {
                const enabled = (questions[currentQuestion].type ?? 'choice') === 'text' ? (textAnswer.trim() !== '' || skipped) : selectedOption !== null;
                if (enabled) {
                  e.currentTarget.style.backgroundColor = 'rgb(54, 125, 136)';
                }
              }}
              onMouseLeave={(e) => {
                const enabled = (questions[currentQuestion].type ?? 'choice') === 'text' ? (textAnswer.trim() !== '' || skipped) : selectedOption !== null;
                if (enabled) {
                  e.currentTarget.style.backgroundColor = 'rgb(61, 141, 153)';
                }
              }}
            >
              {currentQuestion === questions.length - 1 ? 'Complete Assessment' : 'Next'}
            </button>
          </div>
        </div>

        {/* Footer */}
        <div className="mt-8 text-center">
          <div className="flex items-center justify-center text-xs text-gray-400">
            <span className="font-semibold">NGS Security Platform</span>
          </div>
        </div>
      </div>
    </div>
  );
}
